import Box from '@mui/material/Box/Box';
import { AppbarContainer } from '@app/ui';
import { AppShell } from '@app/ui';

export default function A11yDanger() {
  return (
    <AppShell>
      <AppbarContainer reload={false}
      titleConf={{ns:"homepage/a11y-danger/content", key:"content.title"}}
        appbarProps={{
          title: '辅助功能危险',
          back: true,
      }}>
        <Box>
          a11y danger
        </Box>
      </AppbarContainer>
    </AppShell>
  );
}
